<script>
import { defineComponent, ref, watch } from 'vue';
import { useRouter } from 'vue-router';

export default defineComponent({
  setup() {
    const loadFooter = ref(true);
    const router = useRouter();

    watch(
      () => router.currentRoute.value.path,
      (path) => {
        if (path === '/cart/' || path === '/order/') {
          loadFooter.value = false;
          setTimeout(() => {
            loadFooter.value = true;
          }, 1000);
        } else if (path === '/' || path === `/order/${router.currentRoute.value.params.id}`) {
          loadFooter.value = false;
          setTimeout(() => {
            loadFooter.value = true;
          }, 300);
        }
      },
      { immediate: true },
    );

    return { loadFooter };
  },
});
</script>
